import React, { Suspense, lazy } from 'react'
import AuthorDetails from '../../Elements/Author/AuthorDetails';
import Media from './Media';
import CTAButton from '../../Elements/CTA/CtaButton';
import SocialAction from '../../Elements/SocialAction/SocialAction';
import InViewWrapper from '../../Elements/InViewWrapper';
const ShareOnHover = lazy(() => import('../../Elements/ShareOnHover/ShareOnHover'));
const Card = ({ postSize, clickToShowPopUp, handlePressStart, handlePressEnd, index, onClickToCTA, ThemeStyle, CardStyle, Post, ThemeInfo }) => {
  const mediaType = Post.type !== 1 ? true : false; 

  return <InViewWrapper>
        {({ ref, inView }) => (
          mediaType ? <div id={`tb_cp_id-${Post.id}`} ref={ref} className={`tb_cp_post_wrapper`} style={{width: `${postSize}%`, padding: CardStyle.style.padding / 2, opacity: inView ? 1 : 0, transition: inView ? 'opacity .5s ease-in-out' : '' }} tb-network={Post.networkId} tb-product={ Post.isShopIcon ? 1 : Post.isHotspot ? 2 : 0} post-id={Post.referenceId ? Post.referenceId : Post.id} feed-id={Post.feedId} tabIndex={0} area-label={Post.content.text} role="article">
          <div className="tb_cp_post_in" 
            onTouchStart={handlePressStart}
            onTouchEnd={(e) => handlePressEnd(e, index, Post)}
            onClick={clickToShowPopUp(index, Post)} role="button" aria-label="Popup" tabIndex={0}>
            {CardStyle.shareStatus ? 
              <Suspense fallback={<></>}> 
                <ShareOnHover Post={Post} ThemeInfo={ThemeInfo} shareClass={'tb_cp_share_container'} />
              </Suspense> 
            : ''}
            {mediaType ? 
                <Media Post={Post} CardStyle={CardStyle} ThemeStyle={ThemeStyle} index={index} inView={inView}/>
            : ''}
            { Post.ctaStatus ? <div className="tb_cp_post_cta">
                <CTAButton ctaClass={'tb_cp_post_cta_btn'} Post={Post} onClickToCTA={onClickToCTA} />
            </div> : ''}
            <div className="tb_cp_contant_wrapper">
                <AuthorDetails className={'tb_cp'} Post={Post} CardStyle={CardStyle} ThemeStyle={ThemeStyle} inView={inView} />
            </div>
            {CardStyle.actionStatus ? <SocialAction Post={Post} CardStyle={CardStyle} actionClass={`tb_cp_social_action__`} /> : ''}
          </div>
        </div>
        :null)}
      </InViewWrapper>
}
export default Card