import React, { PureComponent } from "react";
import Masonry from 'react-masonry-component';
import { isTagShop } from '../../../actions/api'
import "./scss/ClassicPhoto.scss";
import Card from './Elements/Card';
import { getColumns, getSizeInPercent } from "../../../utils";

const ClassicPhotoExtend = ({ Posts, completeDataObject, clickToShowPopUp, handlePressStart, handlePressEnd, postSize, onClickToCTA, onClickPopUpSlider, CardStyle, ThemeStyle, ThemeInfo }) => {
  return ((Posts && Posts.length > 0)) && Posts.map((item, index) => {
    const Post = completeDataObject[item];
    return <Card key={index} postSize={postSize} handlePressStart ={handlePressStart} handlePressEnd={handlePressEnd}  clickToShowPopUp={clickToShowPopUp} onClickToCTA={onClickToCTA} onClickPopUpSlider={onClickPopUpSlider} Post={Post} index={index} CardStyle={CardStyle} ThemeStyle={ThemeStyle}  ThemeInfo={ThemeInfo}/>
  })
}

class ClassicPhoto extends PureComponent {  
      
    state = {
      loadDelaydata : false
    }
    
    componentDidMount() {
      window.addEventListener("scroll", this.handleScroll);
      this.props.loadBranding()
    }
    componentWillUnmount() {
      window.removeEventListener("scroll", this.handleScroll);
    }

    handleScroll = () => {
      if(!this.state.loadDelaydata){
          this.setState({ loadDelaydata : true },()=>this.props.loadMoredata())
      }
    };

  render() {
    const { postData, completeDataObject, CardStyle, ThemeStyle, ThemeInfo, clickToShowPopUp, handlePressStart, handlePressEnd, onClickToCTA, onClickPopUpSlider, containerSize } = this.props;
    const responsiveColumns = Math.max((containerSize.width / (CardStyle.minimumWidth + 20)).toFixed(), 1)
    const columns =  getColumns(containerSize.width, ThemeStyle.column, responsiveColumns);
    const postSize = getSizeInPercent(containerSize.width, columns)


    return isTagShop ? <Masonry
      className={`tb_cp_post_container`}
      tabIndex='0'
      aria-label={`There are ${(postData && postData.length > 0) ? postData.length : 0} posts in the feed`} role="feed"
      elementType={'div'}
      options={{
        transitionDuration: 0,
        percentPosition: true,
        itemSelector: '.tb_cp_post_wrapper',
        horizontalOrder: true,
        resize: true,
      }}
      disableImagesLoaded={false}
      updateOnEachImageLoad={false}
      imagesLoadedOptions={{}}
      enableResizableChildren={true}>
      <ClassicPhotoExtend clickToShowPopUp={clickToShowPopUp} handlePressStart ={handlePressStart} handlePressEnd={handlePressEnd} Posts={postData} completeDataObject={completeDataObject} postSize={postSize} onClickToCTA={onClickToCTA} onClickPopUpSlider={onClickPopUpSlider} CardStyle={CardStyle} ThemeStyle={ThemeStyle} ThemeInfo={ThemeInfo} />
    </Masonry>
    :
    <div className="tb_cp_post_container">
      <ClassicPhotoExtend clickToShowPopUp={clickToShowPopUp} handlePressStart ={handlePressStart} handlePressEnd={handlePressEnd}  Posts={postData} completeDataObject={completeDataObject} postSize={postSize} onClickToCTA={onClickToCTA} onClickPopUpSlider={onClickPopUpSlider} CardStyle={CardStyle} ThemeStyle={ThemeStyle} ThemeInfo={ThemeInfo} />
    </div>
  }
}

export default ClassicPhoto


